import { Component } from '@angular/core';
import { IonicPage, ViewController } from 'ionic-angular';
import { TranslateService } from '@ngx-translate/core';
import { appSettings } from '../../app/app-settings';
import { Storage } from '@ionic/storage';

/**
 * Generated class for the LangModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
	selector: 'page-lang-modal',
	templateUrl: 'lang-modal.html',
})
export class LangModalPage {

	public flagList: string[];
	public flagSel: string;

	constructor(public storage: Storage, public translate: TranslateService, private viewCtrl: ViewController) {
		this.flagList = appSettings.LANGUAGE_LIST;
		storage.get('lang').then((val) => {
			this.flagSel = val;
		});
	}

	closeModal() {
		this.viewCtrl.dismiss();
	}

	setLang(lang) {
		this.storage.set('lang',lang);
		this.flagSel = lang;
		this.translate.use(lang);
	}

}



